.reader {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    user-select: none;
}
.button_close {
    background-color: #EFC26D;
    border: none;
    border-radius: 10px;
    position: absolute;
    right: 30px;
    top: 30px;
    font-family: 'Bodoni', serif;
    color: #181E21;
    box-shadow: 0px 0px 20px rgba(255, 255, 255, 1);
}
@media screen and (max-width: 1920px) {
    .button_close {
        width: 122px;
        height: 42px;
        font-size: 24px;
    }
}
@media screen and (min-width: 1920px){
    .button_close {
        width: 243px;
        height: 85px;
        font-size: 50px;
    }
}

.page {
    background-color: wheat;
}
.zero_page {
    background: rgba(255, 255, 255, .3);
}